<template>
    <div id="new-employee">
        <div class="row">
            <div class="col s12 m6">
                <div class="card">
                    <div class="card-content">
                        <span class="card-title" v-text="employee.name"></span>
                        <span class="chip" v-text="employee.dept"></span>
                        <p v-text="employee.position"></p>
                    </div>
                    <div class="card-action">
                        <router-link to="/">Back</router-link>
                        <button @click="deleteEmployee" class="waves-effect waves-teal btn-flat red-text">Delete</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="fixed-action-btn">
            <router-link class="btn-floating btn-large red" :to="{
                            name: 'edit-employee',
                            params: { employee_id: employee.employee_id }
            }">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: "view-employee",
        data() {
            return {
                employee: {}
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('employees')
                .where('employee_id', '==', to.params.employee_id)
                .get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        next(vm => {
                            vm.employee = doc.data()
                        })
                    })
                })
        },
        methods: {
            deleteEmployee() {
                if(confirm('Are you sure?')) {
                    db.collection('employees')
                        .where('employee_id', '==', this.employee.employee_id)
                        .get()
                        .then(querySnapshot => {
                            querySnapshot.forEach(doc => {
                                doc.ref.delete()
                                this.$router.push('/')
                            })
                        })
                }
            }
        }
    }
</script>

<style scoped>

</style>


