<template>
    <div id="edit-employee">
        <div class="row">
            <h3>Edit employee {{employee.employee_id}}</h3>
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.employee_id" disabled required>
                        <label class="active">Employee ID#</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.name" required>
                        <label class="active">Name</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.dept" required>
                        <label class="active">Department</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.position" required>
                        <label class="active">Position</label>
                    </div>
                </div>

                <button class="btn blue-grey" type="submit">Submit</button>
                <router-link
                        :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}"
                        class="btn blue-grey lighten-5 black-text">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: "edit-employee",
        data() {
            return {
                employee: {}
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('employees')
                .where('employee_id', '==', to.params.employee_id)
                .get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        next(vm => {
                            vm.employee = doc.data()
                        })
                    })
                })
        },
        methods: {
            updateEmployee() {
                db.collection('employees')
                    .where('employee_id', '==', this.employee.employee_id)
                    .get()
                    .then(querySnapshot => {
                        querySnapshot.forEach(doc => {
                            doc.ref
                                .update(this.employee)
                                .then(() => this.$router.push('/' + this.employee.employee_id))
                        })
                    })
            }
        }
    }
</script>

<style scoped>

</style>


