<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="addNewEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.employee_id" required>
                        <label>Employee ID#</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.name" required>
                        <label>Name</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.dept" required>
                        <label>Department</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee.position" required>
                        <label>Position</label>
                    </div>
                </div>

                <button class="btn blue-grey" type="submit">Submit</button>
                <router-link to="/" class="btn blue-grey lighten-5 black-text">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: "new-employee",
        data() {
            return {
                employee: {
                    employee_id: '',
                    name: '',
                    dept: '',
                    position: ''
                }
            }
        },
        methods: {
            addNewEmployee() {
                db.collection('employees')
                    .add(this.employee)
                    .then(docRef => this.$router.push('/'))
            }
        }
    }
</script>

<style scoped>

</style>


