<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Employees</h4>
            </li>
            <li
                    class="collection-item"
                    v-for="employee in employees"
                    v-bind:key="employee.id"
            >
                <div>
                    {{ employee.employee_id }} :
                    {{ employee.name }}
                    <span class="chip" v-text="employee.dept"></span>
                    <router-link :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}" class="secondary-content">
                        <i class="fa fa-eye"></i>
                    </router-link>
                </div>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link class="btn-floating btn-large red" to="/new">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: "dashboard",
        data() {
            return {
                employees: []
            }
        },
        created() {
            db.collection('employees').orderBy('dept').get().then(
                querySnapshot => {
                    querySnapshot.forEach(doc => {
                        const data = {
                            'id': doc.id,
                            'employee_id': doc.data().employee_id,
                            'name': doc.data().name,
                            'dept': doc.data().dept,
                            'position': doc.data().position
                        }
                        this.employees.push(data)
                    })
                }
            )
        }
    }
</script>

<style scoped>

</style>


